const API_KEY = "c4811227a440b1131e7a03d823deefa8";
const BASE_PATH = "https://api.themoviedb.org/3";

interface IShow {
    backdrop_path: string;
    poster_path: string;
    title?: string;
    name?: string;
    overview: string;
    id: number;
}

export interface IGetShow {
    dates: {
        minimum: string;
        maximum: string;
    },
    page: number,
    results: IShow[],
    total_pages: number;
    total_results: number;
}

export interface IShowDetail {
    genres: string[];
}

export function getMovies() {
    return fetch(`${BASE_PATH}/movie/now_playing?api_key=${API_KEY}`).then(
        (response) => response.json()
    );
}

export function getPopular() {
    return fetch(`${BASE_PATH}/movie/popular?api_key=${API_KEY}`).then(
        (response) => response.json()
    );
}

export function getTopRated() {
    return fetch(`${BASE_PATH}/movie/top_rated?api_key=${API_KEY}`).then(
        (response) => response.json()
    );
}

export function getUpcoming() {
    return fetch(`${BASE_PATH}/movie/upcoming?api_key=${API_KEY}`).then(
        (response) => response.json()
    );
}


export function getTv() {
    return fetch(`${BASE_PATH}/tv/on_the_air?api_key=${API_KEY}`).then(
        (response) => response.json()
    );
}

export function getTvPopular() {
    return fetch(`${BASE_PATH}/tv/popular?api_key=${API_KEY}`).then(
        (response) => response.json()
    );
}

export function getAiringToday() {
    return fetch(`${BASE_PATH}/tv/airing_today?api_key=${API_KEY}`).then(
        (response) => response.json()
    );
}


export function getTvTopRated() {
    return fetch(`${BASE_PATH}/tv/top_rated?api_key=${API_KEY}`).then(
        (response) => response.json()
    );
}

export function getShowDetail(type: string, show_id: number) {
    return fetch(`${BASE_PATH}/${type}/${show_id}?api_key=${API_KEY}`).then(
        (response) => response.json()
    );
}

export function getSearchTv(query: string) {
    return fetch(`${BASE_PATH}/search/tv?query=${query}&api_key=${API_KEY}`).then(
        (response) => response.json()
    );
}

export function getSearchMovie(query: string) {
    return fetch(`${BASE_PATH}/search/movie?query=${query}&api_key=${API_KEY}`).then(
        (response) => response.json()
    );
}